.@{container-class} .fn-m-job-input-area{
		margin:10*@rex 0 0 0;
		background:#fff;

	.ui-dl{
		display:flex;
		border-bottom:1*@rex solid @border-color;
		padding:10*@rex 0;
		position:relative;

	.dt{
		flex:2;
		text-align:right;
	}

		.dd{
				flex:6;
				padding-left:10*@rex;
				input{
					border:none;
					width:50%;
				}

				.iconfont{
						position:absolute;
						right:40*@rex;
						bottom:10*@rex;
						color:#cfcfcf;
				}

				.J-job-input{
					padding-right:60*@rex;
					input{width:100%;}
				}
		}
	}
}