@import "./_job.less";
@job-img-dir: 'job/_images';

@import "./module/fn-m-job-button";
@import "./module/fn-m-job-input-area";

.land{
	margin-top:23*@rex;
	height:auto;
	overflow:hidden;
	border-top:1*@rex solid #D5D5D5;


	ul{

		display:block;
		margin-bottom:25*@rex;
		background:#fff;
		position:relative;

		li{
			display:block;
			line-height:50*@rex;
			border-bottom:1*@rex solid #D5D5D5;
			overflow:hidden;

			.l-menu{
				display:block;
				width:80*@rex;
				float:left;
				text-indent:5*@rex;
			}

			input{
				line-height:22*@rex;
				float:left;
				.margin(13*@rex,_,_,_);
				width:70%;
			}

			em{
				display:block;
				background: data-uri('@{job-img-dir}/land.png');
				background-size: 100% 100%;
				.size(15*@rex);
				.absolute(18*@rex,5*@rex,_,_);
			}
		}
	}

	.btn{
		display:block;
		width:90%;
		margin:0 5% 0 5%;
		background:#C20053;
		border-radius:4*@rex;
		line-height:40*@rex;
		color:#fff;
		border:none;
		text-align:center;

	}

	.pass{
		display:block;
		wdith:95%;
		text-align:right;
		margin:5% 5% 0 0;
		color:#666666;
	}

	.register{
		width:20%;
		border:1*@rex solid #C20053;
		border-radius:2*@rex;

		line-height:42*@rex;
		color:#C20053;
		text-align:center;
		margin:10% 40% 1% 40%;

		a{
			color:#C20053;
		}

	}


}
